import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                //!important
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#444"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-Homehomepagemenu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-Homehomepagemenu1'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuchekong'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwucheman'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon'></i>                           
                        }
                        title="订单列表"
                        key="order"
                        selected={ pathname === '/order' }
                        onPress={ () => {
                            this.props.history.push('/order')
                        } }
                        data-seed="logId2"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-yonghu1'></i> }
                        selectedIcon={
                            <i className='iconfont icon-yonghu'></i>                           
                        }
                        title="我的淘宝"
                        key="mine"
                        selected={ pathname === '/mine' }
                        onPress={ () => {
                            this.props.history.push('/mine')
                        } }
                        data-seed="logId3"
                    >
                        
			</TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);